import React, { useState } from "react";
import image from "../../Assets/istockphoto-1320799591-612x612.jpg";
import imagetwo from "../../Assets/logo.png";
import * as Yup from "yup";
import { useFormik } from "formik";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";

export default function Login({ saveuser }) {
  let nav = useNavigate();
  const [errormsg, seterrormsg] = useState("");
  let [loading, setloading] = useState(false);
  let valid = Yup.object({
    email: Yup.string().email("enter a valid email").required(),
    password: Yup.string()
      .required()
      .matches(
        /^[A-Z][a-zA-Z0-9!@#$%^&*()_-]{6,16}$/,
        "Invalid password: must begin with a capital letter and contain a number or a special character"
      ),
  });
  let form = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    onSubmit: (values) => {
      sendlogindata(values);
    },
    validationSchema: valid,
  });

  async function sendlogindata(info) {
    setloading(true);
    let { data } = await axios
      .post(`https://route-ecommerce.onrender.com/api/v1/auth/signin`, info)
      .catch((error) => {
        console.log(error.response.data.message);
        seterrormsg(error.response.data.message);
        setloading(false);
      });
    setloading(false);
    if (data.message == "success") {
      nav("/home");
      saveuser(data.user);
      localStorage.setItem("token", data.token);
    }
  }
  return (
    <div>
      <div className="logincontainer">
        <div className="row login d-flex align-items-center">
          <div className="col-md-6 ">
            <img className="w-100" height={500} src={image} alt="" />
          </div>

          <div className="col-md-6 ">
            <div className="d-flex w-100 justify-content-center">
              <img
                src={imagetwo}
                className="w-25 d-flex align-items-center"
                alt=""
              />
            </div>
            <h2 className="text-muted text-center">Login</h2>
            <form onSubmit={form.handleSubmit}>
              <div className="my-3">
                <label className="text-white" htmlFor="email">
                  E-mail
                </label>
                <input
                  onChange={form.handleChange}
                  type="email"
                  name="email"
                  id="email"
                  className="form-control"
                />
                <p className="text-danger">{form.errors.email}</p>
              </div>
              <div className="my-3">
                <label className="text-white" htmlFor="password">
                  Password
                </label>
                <input
                  onChange={form.handleChange}
                  type="password"
                  name="password"
                  id="password"
                  className="form-control"
                />
                <p className="text-danger">{form.errors.password}</p>
              </div>

              {errormsg ? <p className="text-danger">{errormsg}</p> : ""}
             
             <Link to="/forgetpassword" className="m-2">Reset Password</Link>
             <br/>
             <br/>

              {loading ? (
                <button type="button" className="btn btn-primary w-100 ">
                  <i className="fa-solid fa-spinner fa-spin text-white spinner"></i>
                </button>
              ) : (
                <button
                  disabled={!form.isValid}
                  type="submit"
                  className="btn btn-primary w-100"
                >
                  Login
                </button>
              )}
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
