import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import './Allgames.css'
import Item from '../Item/Item';
export default function AllGames() {
    let [gamelist,setgamelist]=useState([])

    let [visibleitems,setvisibleitems]=useState(28)
    function seemore(){
setvisibleitems(visibleitems+10)
    }
    async function getdata(){
        axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games", { 'headers': { 'X-RapidAPI-Key':'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com' } })
        .then(({data})=>{
    console.log(data);
    setgamelist(data)
        }).catch((error)=>{
    console.log(error.message)
        })
    
      }
      
useEffect(()=>{
    getdata()
    console.log(gamelist)
    },[])
  return (
    <> <div className="row">
    {gamelist.slice(0,visibleitems).map((element)=>{
        return  <Item element={element}/>
    
    })}
    <button onClick={seemore} className='btn btn-primary w-25 m-auto m-2'>See more</button>
    </div>
    </>
  )
}