import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Item from '../Item/Item';

export default function Releasedate() {
const [releasedate,setreleasedate]=useState([])
let [visibleitems,setvisibleitems]=useState(28)
function seemore(){
setvisibleitems(visibleitems+10)
}
    async function getreleasedate(){
        axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games?sort-by=releasedate", { 'headers': { 'X-RapidAPI-Key':'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com' } })
        .then(({data})=>{
          console.log(data);
          setreleasedate(data)
        }).catch((error)=>{
          console.log(error.message)
        })
        
      }
      useEffect(()=>{
        getreleasedate()
      },[])
      return (
        <div className="row">
    {releasedate.slice(0,visibleitems).map((element)=>{
      return  <Item element={element}/>
      
    })}
  <button onClick={seemore} className='btn btn-primary w-25 m-auto m-2'>See more</button>
    </div>
  )
}
