import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';

import Item from '../Item/Item';
export default function Sporting() {
    let [actiongamelist,setactiongamelist]=useState([])
    async function getdata(){
        axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games?category=sports", { 'headers': { 'X-RapidAPI-Key':'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com' } })
        .then(({data})=>{
    console.log(data);
    setactiongamelist(data)
        }).catch((error)=>{
    console.log(error.message)
        })
    
      }
      
useEffect(()=>{
    getdata()
    },[])
  return (
    <> <div className="row">
    {actiongamelist.slice(0,28).map((element)=>{
        return  <Item element={element}/>
    
    })}
    </div>
    </>
  )
}