import React from 'react'
import { Link } from 'react-router-dom'
import './Item.css'
export default function Item({element}) {
  return (
  
    <div className="col-lg-3 col-md-6 col-sm-12 enlarge ">
        <Link to={`/itemdetails/${element.id}`} className='text-decoration-none'>
            <div className="card" >
              <img className="card-img-top dimmedimg" src={element.thumbnail} alt="Card image cap"/>
              <div className="card-body">
                <div className="d-flex align-items-baseline w-100">
                  <h5 className="card-title d-inline-block">{element.title} </h5>
                  <a href="#" className="btn btn-primary w-25 d-inline-block mx-5">Go </a>
                </div>
                <p className="card-text">{element.short_description}</p>
                </div>
              <div className="card-footer text-muted position-relative">
              <span>{element.genre}</span>
              <span className="position-absolute end-0">{element.platform}</span>
            </div>
            </div>
            </Link>
          </div>
  )
}
