import "./App.css";
import { Navigate, RouterProvider, createBrowserRouter } from "react-router-dom";
import Layout from "./Components/Layout/Layout";
import Login from "./Components/Login/Login";
import Signup from "./Components/SignUp/Signup";
import Notfound from "./Components/Notfound/Notfound";
import Home from "./Components/Home/Home";
import { useEffect, useState } from "react";
import jwt_decode from "jwt-decode";
import ForgetPassword from "./Components/ForgetPassword/ForgetPassword";
import ResetPassword from "./Components/ResetPassword/ResetPassword";
import AllGames from "./Components/AllGames/AllGames";
import Platforms from "./Components/Platforms/Platforms";
import ItemDetails from "./Components/ItemDetails/ItemDetails";
import Browser from "./Components/Platforms/Browser";
import PC from "./Components/Platforms/PC";
import Alphabetical from "./Components/Sortby/Alphabetical";
import Popularity from "./Components/Sortby/Popularity";
import Relevance from "./Components/Sortby/Relevance";
import Releasedate from "./Components/Sortby/Releasedate";
import Racing from "./Components/Categories/Racing";
import Sporting from "./Components/Categories/Sporting";
import Social from "./Components/Categories/Social";
import Shooter from "./Components/Categories/Shooter";
import Openworld from "./Components/Categories/Openworld";
import Zombie from "./Components/Categories/Zombie";
import Fantasy from "./Components/Categories/Fantasy";
import Actionrpg from "./Components/Categories/Actionrpg";
import Action from "./Components/Categories/Action";
import Flight from "./Components/Categories/Flight";
import Battleroyale from "./Components/Categories/Battleroyale";


function App() {
  let [userdata, setuserdata] = useState(null);
  function saveuser(data) {
    setuserdata(data);
  }
  useEffect(() => {
    if (localStorage.getItem("token")) {
      let token = localStorage.getItem("token");
      console.log(token)
      
      let data = jwt_decode(token);
      
      //as if u login
      saveuser(data);
    }
  }, []);
   function logout(){
    setuserdata(null)
     localStorage.removeItem("token")
    return <Navigate to="/login"/>
   }
  function Protectedrouting(props) {
    if (localStorage.getItem("token")) {
      return props.children;
    } else {
      return <Navigate to="/login"/>;
    }
  }
  let routes = createBrowserRouter([
    {
      path: "",
      element: <Layout userdata={userdata} logout={logout}/>,
      children: [
        { path: "login", element: <Login saveuser={saveuser} /> },
        { path: "signup", element: <Signup /> },
        { path: "forgetpassword", element: <ForgetPassword/> },
        { path: "resetpassword", element: <ResetPassword/> },
        { path: "*", element: <Notfound /> },
        {
          path:"home",
          element: (
            <Protectedrouting>
              <Home userdata={userdata} />
            </Protectedrouting>
          ),
        },
        {
          path:"allgames",
          element: (
            <Protectedrouting>
              <AllGames />
            </Protectedrouting>
          ),
        },
        {
          path:"platform",
          element: (
            <Protectedrouting>
              <Platforms/>
            </Protectedrouting>
          ),
        },
        {
          path:"itemdetails/:id",
          element: (
            <Protectedrouting>
              <ItemDetails/>
            </Protectedrouting>
          ),
        },
        {
          path:"browser",
          element: (
            <Protectedrouting>
              <Browser/>
            </Protectedrouting>
          ),
        },
        {
          path:"pc",
          element: (
            <Protectedrouting>
              <PC/>
            </Protectedrouting>
          ),
        },
        {
          path:"alphabetical",
          element: (
            <Protectedrouting>
              <Alphabetical/>
            </Protectedrouting>
          ),
        },
        {
          path:"popularity",
          element: (
            <Protectedrouting>
              <Popularity/>
            </Protectedrouting>
          ),
        },
        {
          path:"relevance",
          element: (
            <Protectedrouting>
              <Relevance/>
            </Protectedrouting>
          ),
        },
        {
          path:"releasedate",
          element: (
            <Protectedrouting>
              <Releasedate/>
            </Protectedrouting>
          ),
        },
        {
          path:"racing",
          element: (
            <Protectedrouting>
              <Racing/>
            </Protectedrouting>
          ),
        },
        {
          path:"sporting",
          element: (
            <Protectedrouting>
              <Sporting/>
            </Protectedrouting>
          ),
        },
        {
          path:"social",
          element: (
            <Protectedrouting>
              <Social/>
            </Protectedrouting>
          ),
        },
        {
          path:"shooter",
          element: (
            <Protectedrouting>
              <Shooter/>
            </Protectedrouting>
          ),
        },
        {
          path:"openworld",
          element: (
            <Protectedrouting>
              <Openworld/>
            </Protectedrouting>
          ),
        },
        {
          path:"zombie",
          element: (
            <Protectedrouting>
              <Zombie/>
            </Protectedrouting>
          ),
        },
        {
          path:"fantasy",
          element: (
            <Protectedrouting>
              <Fantasy/>
            </Protectedrouting>
          ),
        },
        {
          path:"actionrpg",
          element: (
            <Protectedrouting>
              <Actionrpg/>
            </Protectedrouting>
          ),
        },
        {
          path:"action",
          element: (
            <Protectedrouting>
              <Action/>
            </Protectedrouting>
          ),
        },
        {
          path:"flight",
          element: (
            <Protectedrouting>
              <Flight/>
            </Protectedrouting>
          ),
        },
        {
          path:"battleroyale",
          element: (
            <Protectedrouting>
              <Battleroyale/>
            </Protectedrouting>
          ),
        },
      ],
    },
  ]);
  return <RouterProvider router={routes} />;
}

export default App;
