import React from 'react'
import logoimg from '../../Assets/logo.png'
import { Link } from 'react-router-dom'
import './Navbar.css'

export default function Navbar({userdata,logout}) {
  return (
<>
<nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    
        <img src={logoimg} className='customwidth' alt="" />
    <a className="navbar-brand text-white" href="#">Game Over</a>
    
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
       {userdata?<ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active text-white" aria-current="page" to="/home">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link text-white" to="allgames">All</Link>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link dropdown-toggle text-white" to="platform" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Platforms
          </Link>
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" to="pc">PC</Link></li>
            <li><Link className="dropdown-item" to="browser">Browser</Link></li>
          </ul>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link dropdown-toggle text-white" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Sort by
          </Link>
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" to="releasedate">Release Date</Link></li>
            <li><Link className="dropdown-item" to="popularity">Popularity</Link></li>
            <li><Link className="dropdown-item" to="alphabetical">Alphabetical</Link></li>
            <li><Link className="dropdown-item" to="relevance">Relevance</Link></li>
          </ul>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link dropdown-toggle text-white" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
             Categories
          </Link>
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" to="racing">Racing</Link></li>
            <li><Link className="dropdown-item" to="sporting">Sporting</Link></li>
            <li><Link className="dropdown-item" to="social">Social</Link></li>
            <li><Link className="dropdown-item" to="shooter">Shooter</Link></li>
            <li><Link className="dropdown-item" to="openworld">Open-world</Link></li>
            <li><Link className="dropdown-item" to="zombie">Zombie</Link></li>
            <li><Link className="dropdown-item" to="fantasy">Fantasy</Link></li>
            <li><Link className="dropdown-item" to="actionrpg">Action-rpg</Link></li>
            <li><Link className="dropdown-item" to="action">Action</Link></li>
            <li><Link className="dropdown-item" to="flight">Flight</Link></li>
            <li><Link className="dropdown-item" to="battleroyale">Battle-royale</Link></li>
          </ul>
        </li>

      </ul>:""}
      
      
    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
{userdata==null?<>

<li className="nav-item">
          <Link className="nav-link active text-white" aria-current="page" to="/login">Login</Link>
        </li>
        <li className="nav-item">
        <Link className="nav-link active  btn btn-outline-info " aria-current="page" to="/signup">
       
        Join Free
       
        
        </Link>
         </li>
</>
:
<li className="nav-item">
<button className='nav-link btn btn-outline-danger text-white' onClick={logout}>Logout</button>
</li>}

    </ul>
    </div>
  </div>
</nav>
    </>
    
  )
}
