import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import './ItemDetails.css'
export default function ItemDetails() {
    
    let {id}=useParams()
    const [itemdetails,setitemdetails]=useState([])
    async function getitemdetails(id){
        //https://free-to-play-games-database.p.rapidapi.com/api/game

        axios.get(`https://free-to-play-games-database.p.rapidapi.com/api/game?id=${id}`,
        { 'headers': { 'X-RapidAPI-Key':'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com' } })
        .then(({data})=>{
    console.log(data);
    setitemdetails(data)

        }).catch((error)=>{
    console.log(error)
        })
    }
    useEffect(()=>{
        getitemdetails(id)

        // console.log(itemdetails.minimum_system_requirements.os);
    },[])
  return (
    <div>
        <div className="row">
            <div className="col-md-4 col-sm-12">
                <img src={itemdetails.thumbnail} alt="" />
                <button className='btn btn-primary w-75'><a href={itemdetails.game_url} className='text-white'>PLAY NOW</a></button>
            </div>
            <div className="col-md-8 col-sm-12">
                <h1 className='text-muted'>{itemdetails.title}</h1>
                <h4 className='text-muted'>About {itemdetails.title}</h4>
                <p className='text-muted'>{itemdetails.description}</p>
                <h3 className='text-muted'>Minimum system requirements</h3>
                <p className='text-muted'>Graphics {itemdetails.minimum_system_requirements?.graphics}</p> 
                <p className='text-muted'>Memory {itemdetails.minimum_system_requirements?.memory}</p>
                <p className='text-muted'>Operating systems {itemdetails.minimum_system_requirements?.os}</p>
                <p className='text-muted'>Processor {itemdetails.minimum_system_requirements?.processor}</p>
                <p className='text-muted'>Storage {itemdetails.minimum_system_requirements?.storage}</p>  
                <h2 className='text-muted'>{itemdetails.title} Screenshots</h2>
                <>
                <OwlCarousel className='owl-theme' loop margin={10} items={1} dots={false} autoplay={true}>
    <div class='item'>
        <h4> </h4>
         {itemdetails.screenshots  ? <img src={itemdetails?.screenshots[0]?.image}  className='w-100'  alt="" /> :""} 
        {/* {undefined ?? <img src={itemdetails.screenshots[0]?.image}  className='w-100' alt="" />} */}
    </div>
     <div class='item'>
        <h4>
         {itemdetails.screenshots  ? <img src={itemdetails?.screenshots[1]?.image}  className='w-100'   alt="" /> :""} 
           </h4>
    </div>
    <div class='item'>
     {itemdetails.screenshots? <img src={itemdetails.screenshots[2]?.image}  className='w-100'  alt="" /> :""} 
    </div>
     
</OwlCarousel>;

                </>
                <h2 className='text-muted'>Additional information</h2>
                <div className="row">
                    <div className="col-md-4 col-sm-2" >
                    <p className='text-muted'>Title</p>
                    <p className='text-muted'>{itemdetails.title}</p>
                    </div>
                    <div className="col-md-4 col-sm-2" >
                    <p className='text-muted'>Release Date</p>
                    <p className='text-muted'>{itemdetails.release_date}</p>
                    </div>
                    <div className="col-md-4 col-sm-2" >
                    <p className='text-muted'>Publisher</p>
                    <p className='text-muted'>{itemdetails.publisher}</p>
                    </div>
                    <div className="col-md-4 col-sm-2" >
                    <p className='text-muted'>Genre</p>
                    <p className='text-muted'>{itemdetails.genre}</p>
                    </div>
                    <div className="col-md-4 col-sm-2" >
                    <p className='text-muted'>Genre</p>
                    <p className='text-muted'>{itemdetails.platform}</p>
                    </div>

                </div>

            </div>
        </div>
    </div>
  )
}
