import React from 'react'
import Navbar from '../Navbar/Navbar'
import { Outlet } from 'react-router-dom'

export default function Layout({userdata,logout}) {
  return (
    <div>
        <Navbar userdata={userdata} logout={logout}/>
        <div className="container">
            <Outlet/>
        </div>
    </div>
  )
}
