import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Item from '../Item/Item';

export default function Platforms() {

    let[browser,setbrowser]=useState([])
    let[pc,setpc]=useState([])
    async function getgamebyplatform(platformtype,callback){
        axios.get(`https://free-to-play-games-database.p.rapidapi.com/api/games?platform=${platformtype}`, 
        { 'headers': { 'X-RapidAPI-Key':'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com' } })
        .then(({data})=>{
            callback(data);

            //console.log(data);
            
        }).catch((error)=>{
    console.log(error)
        })
    
    }
    useEffect(()=>{
        getgamebyplatform("browser",setbrowser)
        getgamebyplatform("pc",setpc)
        console.log(browser)
       // console.log("pc"+pc)

        
    },[])
  return (
    <div className='row'>
        {browser.slice(0,28).map((element)=>{
            return <Item element={element}/>
        })}
        {pc.slice(0,28).map((element)=>{
            return <Item element={element}/>
        })}
        
    </div>
  )
}
