import React, { useState } from 'react'
import './ForgetPassword.css'
import * as Yup from 'yup'
import { useFormik } from 'formik'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
export default function ForgetPassword() {
    let[reseterrrormsg,setreseterrrormsg]=useState('')
    let[reseterrrormsgcode,setreseterrrormsgcode]=useState('')
    let nav=useNavigate()
    let [codeflag,setcodeflag]=useState(true)
    let validationSchema=Yup.object({
        email:Yup.string().required().email("enter valid email")
    })
   let resetform =useFormik({
    initialValues:{
        email:""
    },
    onSubmit:(vals)=>{
      sendresetinfo(vals)
    },validationSchema
   })
   ////second from
   let codeform =useFormik({
    initialValues:{
        resetCode:""
    },
    onSubmit:(vals)=>{
console.log(vals)
sendresetcode(vals)
    }
   })
  async function sendresetinfo(info){
    let {data}= await axios.post('https://route-ecommerce.onrender.com/api/v1/auth/forgotPasswords',info).catch((error)=>{
    console.log(error.response.data.message)
    setreseterrrormsg(error.response.data.message)
    })
    console.log(data);
    if(data.statusMsg=='success'){
        setcodeflag(false)
    }
   }
   ////
   async function sendresetcode(info){
    let {data}= await axios.post('https://route-ecommerce.onrender.com/api/v1/auth/verifyResetCode',info).catch((error)=>{
    console.log(error.response.data.message)
    setreseterrrormsgcode(error.response.data.message)
    })
    console.log(data);
   if(data.status=='Success'){
    //go to reset pass
    nav("/resetpassword")
   }
   }
  return (
    <div>
        {codeflag?  <form onSubmit={resetform.handleSubmit}className='d-flex flex-column justify-content-center align-items-center d-flex justify-content-center align-items-center flex-column  ' id='resetform'>
        <label  htmlFor="email" className='text-white'>E-mail</label>
        <input onChange={resetform.handleChange} type="email" name='email' id='email' className='form-control' />
    <p className='text-danger'>{resetform.errors.email}</p>
    {reseterrrormsg?    <p className='text-danger'>{reseterrrormsg}</p>:""}

   <button type='submit' className='btn btn-primary'>Send Email</button>
    </form>
:
<form onSubmit={codeform.handleSubmit} className='d-flex flex-column justify-content-center align-items-center d-flex justify-content-center align-items-center flex-column  ' id='resetformtwo'>
    <label  htmlFor="resetCode" className='text-white'>Reset Code</label>
    <input onChange={codeform.handleChange} type="text" name='resetCode' id='resetCode' className='form-control my-3' />
    {reseterrrormsgcode?    <p className='text-danger'>{reseterrrormsgcode}</p>:""}
     <button type='submit' className='btn btn-primary '>Verify</button>
</form>}
  

    
    </div>
  )
}
